import * as player from '../../utils/music/play.js'
export default {
	data() {
		return {
			active:0,
			query:'',
			queryTimer:null,
			searchList:[],
			discList:[]
		};
	},
	created(){
		//获取全局属性
		const data = getApp().globalData;
		uni.showLoading({
		    title: '加载中'
		});
		this.$Api.getDiscList().then(res=>{
			this.discList = res;
			uni.hideLoading();
		})
	},
	methods:{
		changeTab(event){
			this.active = event.detail.current 
		},
		searchSong(){
			clearTimeout(this.queryTimer);

			this.queryTimer = setTimeout(()=>{
				console.log(this.query)
				this.$Api.querySong(this.query).then(res=>{

					this.searchList = res;
				})
			},500)
		},
		async searchItemPlay(item){

			const result = await player.playMusic(item);
			if(result != "success"){
				uni.showToast({
					title:"收费歌曲",
					icon:"none"
				})
			}else{
				uni.showToast({
					title:"播放成功",
					icon:"success"
				})
				
				this.searchList = []
			}
			
		},
		toDisc(item){
			console.log('跳转')
			uni.navigateTo({
			    url: '/pages/discDetail/discDetail?dissid='+item.dissid,
				success:function(res){
					console.log(res)
				},
				fail:function(err){
					console.log(err)
				}
			});
			// this.$Api.getSongList(item.dissid).then()
		}
		
	}
}
